.img-inputer{position:relative;display:inline-block;width:260px;height:150px;border-radius:5px;background:#f0f0f0;box-shadow:0 1px 6px rgba(0,0,0,0.117647),0 1px 4px rgba(0,0,0,0.117647);transition:0.3s cubic-bezier(0.4, 0, 0.2, 1)
}
.img-inputer--small{width:160px;height:100px
}
.img-inputer--small .img-inputer__placeholder{font-size:12px;top:65%
}
.img-inputer--small .img-inputer__icon{font-size:28px !important;top:38%
}
.img-inputer--large{width:460px;height:250px
}
.img-inputer:hover{-ms-transform:scale(1.015);transform:scale(1.015);box-shadow:0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12),0 2px 4px -1px rgba(0,0,0,0.3)
}
.img-inputer.nhe:hover{-ms-transform:scale(1);transform:scale(1);box-shadow:0 1px 6px rgba(0,0,0,0.117647),0 1px 4px rgba(0,0,0,0.117647)
}
.img-inputer--light{background:#fbfdff;border:1px solid #97a8be;box-shadow:none
}
.img-inputer--light .img-inputer__icon{color:#97a8be
}
.img-inputer--light:hover{-ms-transform:scale(1);transform:scale(1);box-shadow:none;border-color:#20a0ff
}
.img-inputer__icon{position:absolute;font-size:34px !important;left:50%;top:40%;color:#757575;-ms-transform:translate(-50%, -50%);transform:translate(-50%, -50%)
}
.img-inputer__placeholder{position:absolute;margin:0;font-size:14px;width:100%;color:#aaa;top:62%;text-align:center
}
.img-inputer__preview-box{position:absolute;top:0;bottom:0;left:0;right:0;border-radius:5px;overflow:hidden;background:#333
}
.img-inputer__preview-img{position:relative;top:0;z-index:2;width:100%
}
.img-inputer__label{position:absolute;top:0;left:0;bottom:0;right:0;z-index:10;cursor:pointer;margin-bottom:0
}
.img-inputer__label:hover ~ .img-inputer__mask{display:block
}
.img-inputer__inputer{position:absolute;left:-9999px
}
.img-inputer__mask{display:none;position:absolute;z-index:9;top:0;left:0;right:0;bottom:0;border-radius:5px;text-align:center;background:rgba(0,0,0,0.22)
}
.img-inputer__file-name{color:white;font-size:13px;padding-top:10px;margin:0;display:inline-block;max-width:90%;text-overflow:ellipsis;white-space:nowrap;overflow:hidden
}
.img-inputer__change{position:absolute;bottom:0;margin:0;font-size:12px;border-radius:0 0 5px 5px;background:rgba(0,0,0,0.7);text-align:center;color:white;width:100%;padding:9px 0
}
.img-inputer__err{color:#e55;font-size:12px;position:absolute;bottom:-28px;width:100%
}
.fade-enter-active,.fade-leave-active{transition:0.15s cubic-bezier(0.4, 0, 0.2, 1);will-change:trnsform
}
.fade-enter,.fade-leave-active{opacity:0;-ms-transform:translateX(30%);transform:translateX(30%)
}



