
@import '../../node_modules/nprogress/nprogress.css';
html, body, #app {
  height: 100%;
}
body {
  background-color: #f9f9f9;
  margin: 0;
  padding: 40px 0 0 0;
}
* {
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: #00ab6b;
}
form {
  text-align: center;
}
.text-wrap {
  min-height: 150px;
}
.text {
  display: block;
  margin: 20px auto;
  padding: 0 10px;
  background: #fbfdff;
  border: 1px solid #97a8be;
  width: 435px;
  height: 35px;
  line-height: 35px;
  font-size: 14px;
  cursor: pointer;
}
.text:hover {
  background-color: #f0f0f0;
}
.copyright {
  text-align: center;
}



